.services{
grid-column: col-start 1 / span 12;
display:grid;
grid-template-columns: 1f;
grid-template-rows:auto;
@include text-template($small,$font-family-ads, $color-dark);
//justify-items:center;
grid-gap:2rem;
@include background($the-great-calm,$header-screen-color,.5,cover);
background-attachment:fixed;

&__header{
grid-column: 1;
grid-row:1;
width:100%;
background:$color-primary;
@include text-template($small,$font-family-ads, $color-primary);
background:$bh-green-3;
background:$color-band;
@include responsive(tablet){
grid-column: 1 / span 3;
}
@include responsive(laptop){
grid-column: 1 / span 5;
}
}//header

&__development{
grid-column: 1;
grid-row:2;
@include responsive(tablet){
}
@include responsive(laptop){
grid-column: 2;
}
}//end development


&__web-starter{
grid-column: 1;
grid-row:3;
@include responsive(tablet){
grid-column: 2;
grid-row: 2;
}
@include responsive(laptop){
grid-column: 3;
}
}//end web-starter


&__troubleshoot{
grid-column: 1;
grid-row:4;
@include responsive(tablet){
grid-column: 3;
grid-row: 2;
}
@include responsive(laptop){
grid-column: 4;
}
}//end troubleshoot

&__design{
grid-column: 1;
grid-row:5;
@include responsive(tablet){
grid-column: 1;
grid-row: 3;
}
@include responsive(laptop){
grid-column: 2;
//grid-row: 12;
}
}//end development


&__content{
grid-column: 1 ;
grid-row:6;
@include responsive(tablet){
grid-column: 2;
grid-row: 3;
}
@include responsive(laptop){
grid-column: 3;
}
}//end web-starter


&__refactoring{
grid-column: 1;
grid-row:7;
@include responsive(tablet){
grid-column: 3;
grid-row: 3;
}
@include responsive(laptop){
grid-column:4;
}
}//end refactor


&__contact{
grid-column: 1;
grid-row:8;
margin-bottom:1rem;
@include responsive(tablet){
grid-column:2;
grid-row: 4;
}
@include responsive(laptop){
grid-column:3;
grid-row: 4;
}
}//end contact


@include responsive(tablet){
grid-template-columns: 1fr 1fr 1fr;


}

@include responsive(laptop){
grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
min-height:100vh;
}


}//end services

.services section:nth-child(n + 2) {
border-radius: $header-b-rad;
padding: 1.2rem;
//background:$color-primary;
display:grid;
//align-items:center;
//justify-content:center;
text-align:center;
@include text-template($small,$font-family-ads, $color-primary);
@include transition;
//
}//end services children

.services section:nth-child(n + 2):nth-child(-n + 7):hover {
box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.9);
transform: scale(1.2);
}

.icon-service{
font-size:40px;
color:$bh-green-1;
//margin:5% -5% 10% -5%;
}
