//in grad has grid

.mission{
grid-column: col-start 1 / span 12;
display:grid;
grid-template-columns: repeat(12, [col-start] 1fr);
grid-template-rows:auto;
//background:$color-dark;

// navigation in mission grid no grid
&__navigation{
grid-column: col-start 1/span 12;
grid-row:1;
background:$color-primary;
margin:2rem;

border-radius:1rem;
@include responsive(tablet){
grid-column: col-start 1 / span 5;
padding:1rem;



}//end tablet
}//end navigation

//in mission grid has grid
&__brands{
grid-column: col-start 1/span 12;
grid-row:2;
display:grid;
grid-template-columns:1fr 1fr;
grid-template-rows:auto;
grid-gap:2rem;
margin:2rem;
@include responsive(tablet){
grid-column: col-start 6 / span 12;
grid-row: 1;
}

&__wp{
grid-column:1 / span 2;
grid-row:1;
background:$color-primary;
border-radius:$header-b-rad;
@include responsive(tablet){
grid-column: 1;
grid-row: 1;
}
}//wp

&__avada{
grid-column:1 / span 2;
grid-row:2;

background:$color-primary;
border-radius:$header-b-rad;

@include responsive(tablet){
grid-column: 2;
grid-row: 1;
}
}//avada

&__woocom{
grid-column:1 / span 2;
grid-row:3;
background:$color-primary;
border-radius:$header-b-rad;

@include responsive(tablet){
grid-column: 1;
grid-row: 2;
}
}//woocom

&__hugo{
grid-column:1 / span 2;
grid-row:4;
background:$color-primary;
border-radius:$header-b-rad;

@include responsive(tablet){
grid-column: 2;
grid-row: 2;
}

}
}//end brands


@include responsive(small-phone){
grid-column: col-start / span 12;
//grid-row: 12;
}

@include responsive(phone){
grid-column: col-start 1 / span 12;
}

@include responsive(tablet){
//grid-template-columns: 1fr 1fr;
}

}//end mission
