$mogb:#4C5D73;
$vligb:#90B0D9 ;
$gwh:#ECEDEC ;
// reds
//$dr-red-1:#800000ff;


overflow-x:hidden;
box-sizing:border-box;
} // end body

.wrapper {
display:grid;
width:100%;
grid-template-columns: repeat(12, [col-start] 1fr);
grid-template-rows:auto;

