.team{
grid-column: col-start 1 / span 12;
display:grid;
grid-template-columns: 1f;
grid-template-rows:auto;
@include text-template($small,$font-family-ads, $color-primary);
//grid-gap:2rem;
//background:$color-dark;

//team sub-grid media queries
@include responsive(phone){
grid-template-columns: 1fr 1fr ;
}

@include responsive(tablet){
grid-template-columns: 1fr 1fr 1fr 1fr;
}

&__header{
grid-column:1;
grid-row:1;
min-width:100%;
background:$bh-green-3;
background:$color-band;
@include responsive(phone){
grid-column: 1 / span 2 ;
}
@include responsive(laptop){
grid-column: 1 / span 4 ;
}
}//end header


&__person__1{
grid-column:1;
grid-row:2;
background:red;
@include responsive(phone){
grid-column: 1;
}
@include responsive(laptop){
}
}//end person 1


&__person__2{
grid-column:1;
grid-row:3;
background:blue;
@include responsive(phone){
grid-column:2;
grid-row:2;
}
@include responsive(laptop){
grid-column: 2;
}
}//end person 2


&__person__3{
grid-column:1;
grid-row:4;
background:orange;
@include responsive(phone){
grid-column:1 ;
grid-row:3;
}
@include responsive(laptop){
grid-column:3;
grid-row:2;
}
}//end person 3


&__person__4{
grid-column:1;
grid-row:5;
background:purple;
@include responsive(phone){
grid-column:2;
grid-row:3;
}
@include responsive(laptop){
grid-column:4 ;
grid-row:2;
}
}//end person 4

}//team

.team section:nth-child(n + 2) {
border-radius: $header-b-rad;
padding: 1.2rem;
margin:2rem;
//background:$color-primary;
//display:grid;
//align-items:center;
//justify-content:center;
text-align:center;

//@include transition;
//
}//end services children
