.header {

//grid cell
grid-column: col-start/span 12;
grid-row: 2;

//grid template
display:grid;
grid-template-columns:1fr;
grid-template-rows:auto;

//dimensions
height:93vh;
width:100%;

//background
@include background($header-img,$header-screen-color,$header-img-screen,cover);

//logo
.logo-box{
grid-column: 1;
grid-row:1;
display:grid;
grid-template-columns:1fr;
grid-template-rows:1fr 1fr 1fr;

//color: $color-primary;
width:60%;
//padding: 0px 2px 20px 2px !important;
//background:none;

.logo {
@include background($logo1, $color-primary,0, contain);
grid-column:1;
grid-row:1;
//margin: 5rem, 0, 0, 1rem;
//width:100%;
//max-width:500px;
align-items:center;
justify-content:left;
//display:flex;
//align-self:center;

&__img{
//max-width:100%;
//margin:auto;
//width:100%;
//height:200px
}
}
}// end logo-box

//banner box
.banner-box {
display:none;
@include text-template($small,$font-family-ads, $color-dark);
//banner box media query
@include responsive(tablet){
//grid-column: 2;
display:grid;
}

//banner-grid
.banner{
//grid-template
grid-template-columns:2fr 1.2fr;
grid-template-rows:auto;
//this variable below controls transparency for entire box
@include background(none, $color-primary,1, none);
display:grid;
//grid-gap:1rem;
//grid-horizontal-gap:1rem;

line-height:2;
height: auto;
width:90%;
border-radius: $header-b-rad;
padding: 1.2rem;
margin:auto;


&__head {
grid-column: 1 / span 2 ;
grid-row:1;
//border-radius: $header-b-rad;
@include background(none, $color-primary,$banner-screen, none);
}


&__check{
grid-column:1;
grid-row:2;
@include responsive(tablet){
grid-column: 1;
@include background(none, $color-primary,$banner-screen, none);
//border-radius: $header-b-rad;

}
}

&__image{
//display:none;
@include background($banner-img, $color-primary, 0 , cover);
//background:$color-primary;
//border-radius: $header-b-rad;
@include responsive(tablet){
display:block;
grid-column:2;
grid-row:2;

}//responsive

.img{
width:150%;
position:relative;
object-fit:fill;
display:flex;
//max-width:unset;
}

}//end banner image




&__call{
display:block;
grid-column:1 / span 2;
grid-row:3;
@include background(none, $color-primary,$banner-screen, none);
//border-radius: $header-b-rad;

.phone-splash{
color:$color-purple ;
font-size:$xlarge;
}
}
}//end banner-grid


}// end banner-box





// media queries

@include responsive(small-phone){
grid-column: col-start/span 12;
grid-row: 2;
}

@include responsive(phone){
grid-column: col-start/span 12;
grid-row:2;
}

//768

@include responsive(tablet){
grid-row:1;
grid-template-columns:1fr 1fr;
}

}//end header
